// app/admin/difusiones/page.tsx
'use client';

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardHeader, CardContent } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { Megaphone } from 'lucide-react';

/* -------------------------------------------------------------------------- */
/*                       Mini-componente confeti “light”                      */
/* -------------------------------------------------------------------------- */

const SimpleConfetti = ({ active }: { active: boolean }) => {
  const [drops, setDrops] = useState<number[]>([]);
  useEffect(() => {
    if (!active) return;
    setDrops(Array.from({ length: 30 }, () => Math.random()));
    const t = setTimeout(() => setDrops([]), 3_000);
    return () => clearTimeout(t);
  }, [active]);

  return (
    <div className="pointer-events-none fixed inset-0 overflow-hidden">
      {drops.map((r, i) => (
        <div
          key={i}
          className="absolute h-2 w-2 rounded-full bg-pink-300 animate-fall"
          style={{ left: `${r * 100}vw`, animationDelay: `${r * 2}s` }}
        />
      ))}
      <style jsx>{`
        @keyframes fall {
          to {
            transform: translateY(100vh) rotate(360deg);
            opacity: 0;
          }
        }
        .animate-fall {
          animation: fall 2s ease-out forwards;
        }
      `}</style>
    </div>
  );
};

/* -------------------------------------------------------------------------- */
/*                              Constantes UI                                 */
/* -------------------------------------------------------------------------- */

const MAX_CHARS   = 500;
const RANDOM_TIPS = [
  'Tip Apu: ¡Mis buñuelos vuelan más rápido que estas ofertas! 🍩',
  'Tip Apu: Si no envías ahora, me comeré estas promos. 😉',
  'Tip Apu: Un buen asunto conquista buzones como un Squishee. 🥤',
];
const STICKERS    = ['🍩', '🥤', '🛒'];

/* -------------------------------------------------------------------------- */
/*                                Componente                                  */
/* -------------------------------------------------------------------------- */

export default function DifusionesPage() {
  const [subject, setSubject] = useState('');
  const [body,    setBody]    = useState('');
  const [isSending, setIsSending] = useState(false);
  const [progress,  setProgress]  = useState(0);
  const [showFx,    setShowFx]    = useState(false);
  const [tip,       setTip]       = useState('');

  useEffect(() => {
    setTip(RANDOM_TIPS[Math.floor(Math.random() * RANDOM_TIPS.length)]);
  }, []);

  /* Simulación de envío + barra de progreso */
  const handleSend = () => {
    setIsSending(true);
    setProgress(0);

    const int = setInterval(() => {
      setProgress((p) => {
        if (p >= 100) {
          clearInterval(int);
          setIsSending(false);
          setSubject('');
          setBody('');
          setShowFx(true);
          setTimeout(() => setShowFx(false), 3_000);
          return 0;
        }
        return p + 25;
      });
    }, 200);
  };

  const remaining = MAX_CHARS - body.length;

  /* ------------------------------ UI ---------------------------------- */
  return (
    <section className="relative min-h-screen bg-[#e9d29a] p-6 pb-22">
      {showFx && <SimpleConfetti active={showFx} />}

      {/* Burbuja “Apu” */}
      <Speech>
        Apu al habla: usa esta sección para enviar difusiones tan explosivas como
        nuestras ofertas. ¡La promoción más jugosa desde que doblamos el precio del
        Squishee de uva!
      </Speech>

      {/* Form principal */}
      <div className="mx-auto max-w-full overflow-x-hidden rounded-lg bg-gradient-to-br from-[#fdea5e] to-[#f8d42c] px-2 pb-2 pt-0">
        <Card className="border-4 border-transparent bg-white shadow-2xl transition hover:border-yellow-500">
          <CardHeader className="flex items-center gap-3 rounded-t-lg border-b-2 border-yellow-500 bg-white p-4">
            <Megaphone size={24} className="text-green-500" />
            <h1 className="text-3xl font-bold text-green-500">Enviar Difusión</h1>
          </CardHeader>

          <CardContent className="space-y-6 bg-white p-6 rounded-b-lg">
            {/* Inputs */}
            <InputBlock
              id="subject"
              label="Asunto"
              value={subject}
              onChange={setSubject}
              placeholder="Título irresistible"
            />
            <TextareaBlock
              value={body}
              onChange={setBody}
              remaining={remaining}
            />

            {/* Stickers */}
            <div className="flex gap-4">
              {STICKERS.map((s) => (
                <button
                  key={s}
                  type="button"
                  onClick={() => setBody((b) => `${b} ${s}`)}
                  className="text-3xl transition-transform hover:scale-110"
                >
                  {s}
                </button>
              ))}
            </div>

            {/* Progreso + botón */}
            <ProgressBar percent={progress} />
            <Button
              className="w-full bg-sky-600 hover:bg-blue-600"
              onClick={handleSend}
              disabled={!subject || !body || isSending}
            >
              {isSending ? 'Enviando…' : 'Enviar a todos'}
            </Button>

            {/* Tip aleatorio */}
            <p className="mt-4 text-center italic text-purple-400">{tip}</p>
          </CardContent>
        </Card>
      </div>
    </section>
  );
}

/* --------------------------- Helpers UI inline --------------------------- */

const Speech = ({ children }: { children: React.ReactNode }) => (
  <div className="relative mb-6 mt-10 w-full">
    <div className="relative left-4 w-[80%] rounded-lg border-2 border-blue-400 bg-white px-8 py-4 shadow-md">
      <p className="text-lg font-semibold text-gray-800">{children}</p>
      {/* Punta */}
      <div className="absolute -bottom-[10px] left-6 h-0 w-0 border-l-[5px] border-l-transparent border-r-[20px] border-r-transparent border-t-[10px] border-t-blue-400" />
      <div className="absolute -bottom-[8px] left-[calc(1rem+1px)] h-0 w-0 border-l-[5px] border-l-transparent border-r-[20px] border-r-transparent border-t-[8px] border-t-white" />
    </div>
  </div>
);

const InputBlock = ({ id, label, value, onChange, placeholder }: any) => (
  <div>
    <Label htmlFor={id}>{label}</Label>
    <Input
      id={id}
      placeholder={placeholder}
      value={value}
      onChange={(e) => onChange(e.target.value)}
      className="border-2 border-gray-400 bg-gray-100 text-gray-900"
    />
  </div>
);

const TextareaBlock = ({ value, onChange, remaining }: any) => (
  <div>
    <Label>Mensaje</Label>
    <textarea
      rows={6}
      maxLength={MAX_CHARS}
      placeholder="¡Oferta explosiva: 2×1 en Squishees solo hoy!"
      className="w-full rounded-lg border-2 border-gray-400 bg-gray-100 p-3 text-gray-900 focus:ring-2 focus:ring-pink-400"
      value={value}
      onChange={(e) => onChange(e.target.value)}
    />
    <div className="mt-2 flex items-center justify-between text-sm">
      <span>{remaining} caracteres restantes</span>
      {remaining < 100 && <span className="text-red-600">¡Casi sin espacio!</span>}
    </div>
  </div>
);

const ProgressBar = ({ percent }: { percent: number }) => (
  <div className="mb-3 h-3 overflow-hidden rounded-full bg-gray-200">
    <div
      className="h-full bg-green-400 transition-all"
      style={{ width: `${percent}%` }}
    />
  </div>
);
